// ----------------------------------------------------------------------------
// Rules
// ----------------------------------------------------------------------------

// Search container
.md-search {

  // Hide search, if JavaScript is not available.
  .no-js & {
    display: none;
  }

  // [tablet landscape +]: Header-embedded search
  @include break-from-device(tablet landscape) {
    padding: 0.4rem;
    padding-right: 3.2rem;
  }

  // Search modal overlay
  &__overlay {
    display: none;
    pointer-events: none;

    // [tablet portrait -]: Show overlay
    @include break-to-device(tablet portrait) {
      display: block;
      position: absolute;
      top: 0.4rem;
      left: 0.4rem;
      width: 4.0rem;
      height: 4.0rem;
      transform-origin: center;
      transition:
        transform 0.3s 0.1s,
        opacity   0.2s 0.2s;
      border-radius: 2.0rem;                                                    // TODO: correct?
      background: #EEEEEE;                                                      // TODO: put in variable
      opacity: 0;
      overflow: hidden;
      z-index: 1;

      // Expanded overlay
      .md-toggle--search:checked ~ .md-header & {
        transform: scale(40);                                                   // TODO: increase scale on larger devices factor, so the transition remains smooth
        transition:
          transform 0.4s,
          opacity   0.1s;
        opacity: 1;
      }
    }
  }

  // Search modal wrapper
  &__inner {
    width: 100%;

    // [tablet portrait -]: Put search modal off-canvas by default
    @include break-to-device(tablet portrait) {
      position: fixed;
      top: 0;
      left: 100%;
      height: 100%;
      transform: translateX(5%);
      transition:
        left      0s    0.3s,
        transform 0.15s 0.15s cubic-bezier(0.4, 0.0, 0.2, 1.0),
        opacity   0.15s 0.15s;
      opacity: 0;
      z-index: 2;

      // Active search modal
      .md-toggle--search:checked ~ .md-header & {
        left: 0;
        transform: translateX(0);
        transition:
          left      0s    0s,
          transform 0.15s 0.15s cubic-bezier(0.1, 0.7, 0.1, 1.0),
          opacity   0.15s 0.15s;
        opacity: 1;
      }
    }

    // [tablet landscape +]: Make relative for inner positioning
    @include break-from-device(tablet landscape) {
      position: relative;
    }
  }

  // Search form
  &__form {

    // [tablet landscape +]: Header-embedded search
    @include break-from-device(tablet landscape) {
      width: 23.0rem;
      transition: width 0.25s cubic-bezier(0.1, 0.7, 0.1, 1.0);
      border-radius: 0.2rem;

      // Active search field
      [data-md-locked] & {
        width: 66.8rem;
      }
    }
  }

  // Icon
  &__icon {
    position: absolute;
    top: $md-icon-padding;
    left: $md-icon-padding + $md-icon-margin;                                   // TODO: solve in some other way...
    transition: color 0.25s;
    font-size: $md-icon-size;
    cursor: pointer;

    // Set search icon on pseudo class, so it can be overridden for mobile
    // and tablet when the search is rendered in an overlay
    &::before {
      content: "search";
    }

    // [tablet portrait -]: Use back arrow as search icon
    @include break-to-device(tablet portrait) {
      top: 1.6rem;
      left: 1.6rem;

      // Show back arrow instead of search icon
      &::before {
        content: "arrow_back";
      }
    }
  }

  // Search field
  &__input {
    padding: 0 1.6rem 0 7.2rem;
    text-overflow: ellipsis;

    // Placeholder and icon color in active state
    + .md-search__icon,
    &::placeholder {
      color: $md-color-black--light;
    }

    // [tablet portrait -]: Full-screen search bar
    @include break-to-device(tablet portrait) {
      width: 100%;
      height: 5.6rem;
      font-size: 1.8rem;
    }

    // [tablet landscape +]: Header-embedded search
    @include break-from-device(tablet landscape) {
      width: 100%;
      height: 4.0rem;
      padding-left: 4.8rem;
      transition:
        background-color 0.25s,
        color            0.25s;
      border-radius: 0.2rem;
      background: $md-color-black--lighter;
      color: $md-color-white;
      font-size: ms(0);

      // Placeholder color
      + .md-search__icon,
      &::placeholder {
        transition: color 0.25s;
        color: $md-color-white;
      }

      // Hovered search field
      &:hover {
        background: $md-color-white--lightest;
      }

      // Active search field
      [data-md-locked] & {
        border-radius: 0.2rem 0.2rem 0 0;
        background: $md-color-white;
        color: $md-color-black;
        text-overflow: none;

        // Placeholder and icon color in active state
        + .md-search__icon,
        &::placeholder {
          color: $md-color-black--light;
        }
      }
    }
  }
}
