.md-search__output {
  overflow-y: auto; // necessary for rounded borders
  // &::after {
  //   display: block;
  //   content: "";
  //   width: 100%;
  //   height: 20px;
  //   background: red; // TODO: white shadow overlay to fade out result scrolling
  // }

  [data-md-locked] & {
    opacity: 1;
  }

  width: 100%;

  opacity: 0;
  transition: opacity .4s, max-height .4s;


  text-align: left;                                        // TODO: wrap with another div for this effect

  z-index: -1; // ??? wherefor? probably not necessary anymore.
}

@include break-to-device(tablet portrait) {
  .md-search__output {
    position: absolute;
    top: 5.6rem;
    bottom: 0;

    background:
  		linear-gradient(white 10%, rgba(255,255,255,0)), // cover
      linear-gradient(to bottom, rgba(0, 0, 0, 0.25),
      rgba(0, 0, 0, 0.125) 20%,
      rgba(0, 0, 0, 0) 60%); // shadow
  	background-repeat: no-repeat;
  	background-color: white;
  	background-size: 100% 20px, 100% 10px;

  	// Opera doesn't support this in the shorthand
  	background-attachment: local, scroll;
  }
  .md-search-result__item {
    // margin: 0.8rem;
  }
  .md-search-result__link {
    padding: 0 1.6rem;
    // background: white;
    // border-radius: 0.2rem;
    // @include z-depth(2);
  }
  .md-search-result__meta {
    padding-left: 1.6rem;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.07);
  }
}

@include break-from-device(tablet landscape) {

  // Scroll shadow - WIP
  .md-search__output {
    @include z-depth(6);

    background: $md-color-white;
    // color: red;
    border-top: 0.1rem solid $md-color-black--lightest; // TODO: box-shadow inset!
    border-radius: 0 0 0.3rem 0.3rem; // ???
    position: absolute; // must be absolute, or header nav will stretch
    max-height: 0vh; // TODO: can this be done in percent!?!?!?
    [data-md-locked] & {
      max-height: 75vh;
    }

    // Override native scrollbar styles
    &::-webkit-scrollbar {
      width: 0.4rem;
      height: 0.4rem;

      // Style scrollbar thumb
      &-thumb {
        background-color: $md-color-black--lighter;

        // Hovered scrollbar thumb
        &:hover {
          background-color: $md-color-accent;
        }
      }
    }

  	background:
  		linear-gradient(white 10%, rgba(255,255,255,0)), // cover
      linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.2) 20%, rgba(0, 0, 0, 0) 60%); // shadow
  	background-repeat: no-repeat;
  	background-color: white;
  	background-size: 100% 20px, 100% 5px;

  	/* Opera doesn't support this in the shorthand */
  	background-attachment: local, scroll;
  }
}

.md-search-result {


  &__meta {
    color: $md-color-black--light;
    padding-left: 4.8rem;
    padding-right: 1.6rem;
    line-height: 4.0rem;
    font-size: ms(-1);

  }

  &__list {
    margin: 0;
    padding: 0;
    list-style-type: none;
    border-top: 0.1rem solid $md-color-black--lightest;
  }

  &__item {
    // background: yellow;

    // border-top: 0.1rem solid $md-color-black--lightest;
  }


  &__link {
    overflow: auto;
    display: block;
    padding-left: 4.8rem;
    padding-right: 1.6rem;
    transition: background 0.25s;

    &:hover {
      background: transparentize($md-color-accent, 0.9);
    }
  }

  &__title {
    color: $md-color-black;
    font-size: ms(0);
    line-height: 1.4;
    margin-top: 0.5em;
  }

  &__description {
    color: $md-color-black--light;
    font-size: ms(-1);
    line-height: 1.4;
    margin: 0.5em 0;;
  }
}

