@include break-to-device(mobile landscape) {

  .md-search__suggest {
    position: relative;
    z-index: 2;
  }
}

.md-search-term {
  position: relative;
  padding: 0 0.8rem 0 4.8rem;
  line-height: 4.0rem; // don't use line height????
  font-size: 1.6rem;

  &::before {
    @extend %md-icon;

    position: absolute;

    content: "access_time";
    font-size: 2.4rem;
    line-height: 4.0rem; // this sucks...
    left: 1.2rem;
    color: $md-color-black--lighter;
  }
  transition: background .25s;
  cursor: pointer;
  &:hover {
    background: mix($md-color-white, $md-color-primary, 90%);
  }
}









.checklist {

  li {
    position: relative;
    list-style-type: none;

    &::before {
      @extend %md-icon;

      position: absolute;
      appearance: none;
      color: blue;

      content: "check_box";
      font-size: 2.4rem;
    }
  }

  input[type="checkbox"]:checked {
    width: 20px;
    // & ~ .checklist li::after {
    //   color: red;
    // }
  }
}

// Inline code blocks
ins.critic, del.critic, mark {
  margin: 0 0.4rem;
  padding: 0.1rem 0;
  word-break: break-word;
  box-decoration-break: clone;

  border-radius: 0.2rem;

  // &::before {
  //   @extend %md-icon;
  //
  //   color: $md-color-black--light;
  //
  //   font-size: 1.6rem;
  //   padding-right: 0.2rem;
  //   vertical-align: -0.2rem;
  // }
}

ins.critic {
  background: #DDFFDD;
  box-shadow: 0.4rem 0 0 #DDFFDD,
             -0.4rem 0 0 #DDFFDD;
  text-decoration: none;
  // &::before {
  //   content: "add";
  // }
}

del.critic {
  background: #FFDDDD;
  box-shadow: 0.4rem 0 0 #FFDDDD,
             -0.4rem 0 0 #FFDDDD;
             //
            //  &::before {
            //    content: "remove";
            //  }
}

// Not critic-specific!
mark {
  background: #FFFF00;
  box-shadow: 0.4rem 0 0 #FFFF00,
             -0.4rem 0 0 #FFFF00; // ligher yellow...
  overflow: auto; // TODO: remove this, just needed to rebuild
}

.critic.comment {
  margin: 0 0.4rem;
  padding: 0.1rem 0;
  border-radius: 0.2rem;
  background: #F0F0F0;
  color: #37474F;
  // font-size: 85%;
  box-shadow: 0.4rem 0 0 #F0F0F0,
             -0.4rem 0 0 #F0F0F0; // darker than code!!!
  box-decoration-break: clone;

  &::before {
    @extend %md-icon;

    color: $md-color-black--lighter;

    content: "chat";
    font-size: 1.6rem;
    padding-right: 0.2rem;
    vertical-align: -0.2rem;
  }
}

.md-button {
  float: right;
  margin-top: 9px;
  font-size: 13px;
  padding-left: 2.6rem;
  font-weight: 700;
  text-transform: uppercase;
}

.task-list-item {
  list-style-type: none;
}

.task-list-item input {
  margin: 0 4px 0.25em -20px;
  vertical-align: middle;
}
//
//
//
.task-list-item {
  position: relative;
}

.task-list-item input[type="checkbox"] {
  opacity: 0;
}

.task-list-item input[type="checkbox"] + label {
  display: block;
  position: absolute;
  top: 50%;
  left: -24px;
  width: 16px;
  margin-top: -8px;
  height: 16px;
  border-radius: 2px;
  background: #CCC;
}

.task-list-item input[type="checkbox"]:checked + label::before {
  display: block;
  margin-top: -4px;
  margin-left: 2px;
  font-size: 1.2em;
  line-height: 1;
  border-radius: 2px;
  content: "✔";
  color: #1EBB52;
}

.codehilite .hll {
  background: #FFFF00;
  display: block;
  margin: 0 -16px;
  padding: 0 16px;
}

